import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";

import { Scatter } from "react-chartjs-2";
import { format } from "d3-format";

const styles = theme => ({
  canvas: {
    width: "100%",
    height: 145
  },
  scatterPlotDefault: {
    fill: theme.palette.text.secondary
  },
  scatterPlotPrimary: {
    fill: theme.palette.primary.light
  },
  scatterPlotSecondary: {
    fill: theme.palette.secondary.light
  }
});

const palette = {
  bar: [
    "#396AB1",
    "#DA7C30",
    "#3E9651",
    "#CC2529",
    "#535154",
    "#6B4C9A",
    "#922428",
    "#948B3D"
  ],
  line: [
    "#3869B1",
    "#DA7E30",
    "#3F9852",
    "#CC2428",
    "#535055",
    "#6B4C9A",
    "#922427",
    "#958C3D"
  ]
};

class LissajousCurve2 extends React.Component {
  render() {
    const { classes, channel, cook, index } = this.props;
    const isIdle = channel ? channel.isIdle : false;
    const isRunning = channel ? channel.isRunning : false;
    const samples = isRunning
      ? channel.stepSamples
      : isIdle &&
        cook &&
        cook.data &&
        index >= 0 &&
        index < cook.data.length &&
        cook.data[index].samples &&
        cook.data[index].samples.data
      ? cook.data[index].samples.data
      : null;
    const samplesXY =
      samples &&
      samples.map(sample => {
        return { x: sample.voltage, y: sample.current };
      });
    const data = {
      labels: ["Scatter"],
      datasets: [
        {
          fill: true,
          backgroundColor: "rgba(75,192,192,0.4)",
          pointBorderColor: "rgba(75,192,192,1)",
          pointBackgroundColor: "#fff",
          pointBorderWidth: 1,
          pointHoverRadius: 5,
          pointHoverBackgroundColor: "rgba(75,192,192,1)",
          pointHoverBorderColor: "rgba(220,220,220,1)",
          pointHoverBorderWidth: 2,
          pointRadius: 4,
          pointHitRadius: 10,
          data: samplesXY
        }
      ]
    };
    const options = {
      responsive: true,
      legend: {
        display: false
      },
      maintainAspectRatio: false,
      scales: {
        xAxes: [
          {
            ticks: {
              callback: function(value) {
                return format(".3s")(value);
              }
            }
          }
        ],
        yAxes: [
          {
            ticks: {
              callback: function(value) {
                return format(".3s")(value);
              }
            }
          }
        ]
      }
    };

    if (!samples) {
      return <div className={classes.canvas} />;
    }
    return (
      <div className={classes.canvas}>
        <Scatter data={data} options={options} height={null} width={null} />
      </div>
    );
  }
}

LissajousCurve2.propTypes = {
  classes: PropTypes.object.isRequired,
  channel: PropTypes.object,
  cook: PropTypes.object,
  index: PropTypes.number
};

LissajousCurve2.defaultProps = {
  index: 0
};

export default withStyles(styles)(LissajousCurve2);
