import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { format } from "d3-format";

// controls
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";
import Tooltip from "@material-ui/core/Tooltip";

// components
import CookConsole from "./CookConsole";
import StartExpButton from "./StartExpButton";
import StopExpButton from "./StopExpButton";

const styles = theme => ({
  chartOuterContainer145: {
    width: "100%",
    height: 145,
    backgroundColor: "#F5F5F5"
  },
  chartOuterContainer169: {
    paddingBottom: "56.25%" /* 16:9 */,
    position: "relative",
    height: 0
    //backgroundColor: "#F5F5F5",
  },
  chartOuterContainerA4: {
    paddingBottom: "70.70%" /* Landscape 297:210 */,
    position: "relative",
    height: 0
    //backgroundColor: "#F5F5F5",
  },
  chartInnerContainer: {
    position: "absolute",
    top: "0",
    left: "0",
    width: "100%",
    height: "100%"
    //backgroundColor: "#F5F5F5",
  },
  areaPlotDefault: {
    // not working
    fill: "#F5F5F5",
    fillOpacity: 0.25,
    stroke: "#F5F5F5"
  },
  areaPlotPrimary: {
    fill: theme.palette.primary.light,
    stroke: theme.palette.primary.light
  },
  areaPlotSecondary: {
    fill: theme.palette.secondary.light,
    stroke: theme.palette.secondary.light
  },
  scatterPlotDefault: {
    fill: theme.palette.text.secondary
  },
  scatterPlotPrimary: {
    fill: theme.palette.primary.light
  },
  scatterPlotSecondary: {
    fill: theme.palette.secondary.light
  },
  linePlotDefault: {
    stroke: theme.palette.primary.main // this is not working
  },
  referenceLine: {
    stroke: theme.palette.divider
  }
});

// Special characters
const frequencySign = "Hz";

//#region CookPanelWrapped
class CookPanel extends React.Component {
  render() {
    const {
      classes,
      channel,
      cook,
      cookIndex,
      parameters,
      onGoFirst,
      onGoPrevious,
      onGoNext,
      onGoLast,
      onStart,
      onStop,
      onChange
    } = this.props;
    const isIdle = channel ? channel.isIdle : false;
    const isRunning = channel ? channel.isRunning : false;
    const isRunningNoiseLevel = channel ? channel.isRunningNoiseLevel : false;
    const isTooHot = channel ? channel.isTooHot : false;
    const started =
      cook && cook.started && cook.started.moment ? cook.started.moment : null;
    const stateExpression = isRunning
      ? "Measuring @ " + format(".2s")(channel.stepFrequency) + frequencySign
      : isRunningNoiseLevel
      ? "Measuring noise level..."
      : isTooHot
      ? "Heat sink is very hot..."
      : cook && cook.data.length > 0
      ? "Cooked, " + started.fromNow()
      : "Idle";
    return (
      <Card>
        <CardHeader
          subheader={stateExpression}
          action={
            <div className={classes.cardControls}>
              <Tooltip title="Start">
                <StartExpButton
                  disabled={!isIdle}
                  parameters={parameters}
                  onStart={onStart}
                  onChange={onChange}
                />
              </Tooltip>
              <Tooltip title="Stop">
                <StopExpButton disabled={!isRunning} onStop={onStop} />
              </Tooltip>
            </div>
          }
        />
        <CardContent>
          <CookConsole
            cookIndex={cookIndex}
            channel={channel}
            cook={cook}
            onGoFirst={onGoFirst}
            onGoLast={onGoLast}
            onGoNext={onGoNext}
            onGoPrevious={onGoPrevious}
          />
        </CardContent>
      </Card>
    );
  }
}
CookPanel.propTypes = {
  classes: PropTypes.object.isRequired,
  channel: PropTypes.object.isRequired,
  cook: PropTypes.object.isRequired,
  cookIndex: PropTypes.number.isRequired,
  parameters: PropTypes.object.isRequired,
  onGoFirst: PropTypes.func.isRequired,
  onGoLast: PropTypes.func.isRequired,
  onGoNext: PropTypes.func.isRequired,
  onGoPrevious: PropTypes.func.isRequired,
  onStart: PropTypes.func.isRequired,
  onStop: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired
};
export default withStyles(styles)(CookPanel);
